/* ---------- bottom links ---------- */

.links-projects {
    position: absolute;
    bottom: 0;
    left: 1.5rem;
    right: 1.5rem;
    padding-bottom: 1.3rem;
    display: flex;
    justify-content: space-between;
}

.links-projects .button {
    font-size: 1.3rem;
    color: white;
    /* background-color: #504ec7; */
    background-color: #333;
    padding: 6px 13px;
    cursor: pointer;
    border-radius: 2px;
    /* border: solid 1px #504ec7; */
    transition: color 0.3s, background-color 0.3s, border-color 0.3s;
    position: relative;
}

.links-projects .button:hover {
    background-color: rgb(141, 141, 141);
    color: white;
    border-color: rgb(141, 141, 141);
}

.links-projects .show-details, .links-projects .show-front {
    font-size: 1.4rem;
    cursor: pointer;
}

.links-projects .button.gitcode {
    /* border: solid 1px #504ec7; */
    border: solid 1px black;
    background-color: white;
    /* color: #504ec7; */
    color: black;
}

.links-projects .button.gitcode:hover {
    color: rgb(141, 141, 141);
    border-color: rgb(141, 141, 141);
    /* background-color: #252466; */
}

.links-projects .button:not(:last-of-type) {
    margin-right: 10px;
}

.links-projects .show-details {
    position: relative;
    display: flex;
    color: #333;
}

.links-projects .show-details i {
    margin-left: 6px;
    position: relative;
}

.links-projects .show-front {
    position: relative;
    display: flex;
    color: white;
}

.links-projects .show-front i {
    position: relative;
    margin-right: 6px;
}

/* ---------- Card content ---------- */

.card-content {
    /* width: inherit; */
    height: 100%;
    padding: 0 1.5rem;
    position: relative;
}

.card-content h5 {
    /* color: #504ec7; */
    color: #555;
    margin-top: 0;
    padding-top: 16px;
}

.card-content h4 {
    /* color: #373699; */
    color: #333;
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 0.3rem;
}

.card-content img {
    max-width: 100%;
    border: solid 1px #555;
    /* border-radius: 0.5rem; */
    margin-top: 1.1rem;
}

.card-content p {
    font-size: 1.1rem;
    color: #555;
}

.card-content .card-description h5, .card-content .card-description p {
    color: white;
}

.card-content .card-description p {
    font-size: 1.2rem;
}

.card-content .card-description h5 {
    border-bottom: solid 2px white;
    padding-bottom: 10px;
    font-weight: bold;
}

/* ---------- technologies used ---------- */

.card-content .technos {
    /* margin-top: 4.2rem; */
    margin-bottom: 1.7rem;
    display: flex;
    flex-wrap: wrap;
}

.card-content .technos span {
    font-size: 1rem;
    border: solid 1px rgb(34, 34, 34);
    padding: 4px 10px;
    border-radius: 3px;
    background-color: white;
    color: rgb(34, 34, 34);
    display: inline-block;
    margin-bottom: 5px;
}

.card-content .technos span:not(:last-of-type) {
    margin-right: 10px;
}

/* ---------- img-wrapper ---------- */

.container-img {
    max-width: 100%;
    margin-bottom: 2.6rem;
}

.image-wrapper {
    position: relative;
    padding-bottom: 48%;
}

.image-wrapper img {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100%;
}