.nav-items {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    /* transition: left 0.4s, transform 0.4s; */
}

.nav-items.navbar-small {
    left: 1%;
    transform: translateX(0);
}

.nav-items ul {
    margin: 0;
    padding: 0;
    white-space: nowrap;
    list-style: none;
}

.nav-items li {
    display: inline-block;
    position: relative;
    /* cursor: pointer; */
}

.nav-items li a:after {
    /* background: none repeat scroll 0 0 transparent; */
    bottom: -3px;
    content: "";
    display: block;
    height: 3px;
    position: absolute;
    background: rgba(0, 0, 0, 0);
    /* background: #555; */
    transition: background 0.2s ease-out, height 0.3s, width 0.2s;
    width: 55%;
}

.nav-items li a:hover:after, .nav-items li a.selected:after {
    background: #555;
    width: 100%;
    bottom: -3px;
}

.nav-items li a.selected:after {
    height: 3px;
}

.nav-items li .accueil:hover:after, .nav-items li .accueil.selected:after {
    background: #72BBFF;
}

