.nav-items li:not(:last-of-type) {
    margin-right: 90px;
    /* transition: margin-right 0.3s ease-in; */
}

.nav-items li a{
    /* text-transform: uppercase; */
    color: #333;
}

.nav-items.navbar-small li a {
    color: #555;
}

.nav-items.navbar-small li:not(:last-of-type) {
    margin-right: 50px;
}

.nav-socials {
    display: flex;
}

.nav-socials img {
    width: 28px;
    margin: 4px 15px 0 15px;
    opacity: 0.7;
    transition: opacity 0.3s;
}

.nav-socials img:hover {
    opacity: 1;
}

.nav-socials .imgs {
    opacity: 0;
    right: -200px;
    position: relative;
    display: flex;
    margin-left: auto;
}